.wrapper{
    background-color: lightgray;
    width: 100%;
    height: 100vh;
    justify-content: center;
    align-items: center;
    display: flex;
}
.form_container{
    width: 500px;
    height: 500px;
    background-color: rgb(241, 238, 238);
    border-radius: 10px;
    border: 2px solid gray;
}
.input{
    background-color: #fff;
    width: 300px;
    height: 40px;
    border-radius: 15px;
    margin-top: 50px;
    margin-left: 50px;

}
.button:hover{
    background-color: gray;
}
.button{
    height: 50px;
    width: 100px;
    background-color: #fff;
    border-radius: 20px;
    margin-top: 50px;
    margin-left: 150px;
    cursor: pointer;
}